<template>
    <v-container>

        <div style="margin-top:20px;">

        <v-row>
            <v-col
                lg="6"
                md="6"
                sm="12"
                xs="12"
            >
                <v-card 
                style="margin-top: -30px;"
                flat>
                    <v-img 
                        :src="'/images/' + mainEvent.image"
                        ></v-img>
                    <v-card-text style="font-size: 1.1rem; text-align: justify;">{{ mainEvent.mainDesc }}</v-card-text>
                </v-card>
            </v-col>
            <v-col
                lg="6"
                md="6"
                sm="12"
                xs="12"
            >
            <v-card
                flat
                style="padding: 10px; margin-top:-30px"
            >
                <v-card-title>Программа {{ mainEvent.mainTitle }}</v-card-title>

                <v-expand-transition>
                <div v-show="show">
                <v-expansion-panels flat popout accordion multiple>
                    <v-expansion-panel
                        v-for="event in mainEvent.events" 
                        :key="event.id" 
                        style="margin-bottom: 5px; padding: 0px;"
                    >
                    <v-expansion-panel-header>
                        <v-progress-linear
                            v-if="event.isActive == 1"
                            color="green"
                            height="40"
                            :value="timeBar(event.timeStart, event.timeStop)"
                        >
                          <template>
                            <div style="border-radius: 10px !important; font-size: 0.9rem; text-align: left !important; padding-left: 7px;">{{ event.title.substr(0,101) }}</div>
                          </template>
                        </v-progress-linear>
                        <span v-else>
                        {{ event.title.substr(0,101) }}
                        </span> 
                    </v-expansion-panel-header>
                    <v-expansion-panel-content v-if="event.subevents">
                        <v-icon>mdi-clock</v-icon>
                        {{ new Date(event.timeStart).toLocaleTimeString() }} -
                        {{ new Date(event.timeStop).toLocaleTimeString() }}
                        <br />
                        <v-icon>mdi-google-maps</v-icon>{{ event.place }}
                            <v-list-item three-line v-for="subevent in event.subevents" :key="subevent.id">
                                <v-list-item-content>
                                <v-list-item-title>{{ subevent.subTitle }}</v-list-item-title>
                                <v-list-item-subtitle>
                                </v-list-item-subtitle>
                                <v-list-item-subtitle>
                                    <v-icon>mdi-clock</v-icon>
                                    {{ new Date(subevent.subTimeStart).toLocaleTimeString() }} - 
                                    {{ new Date(subevent.subTimeStop).toLocaleTimeString() }}
                                </v-list-item-subtitle>

                                <v-list-item-subtitle>
                                    <v-icon>mdi-google-maps</v-icon>
                                    {{ event.place }}
                                </v-list-item-subtitle>

                                <v-progress-linear
                                    v-if="subevent.isActive == 1"
                                    color="green"
                                    height="30"
                                    style="margin-bottom: 20px;"
                                    :value="timeBar(subevent.subTimeStart, subevent.subTimeStop)"
                                >
                                  <template>
                                    <div style="border-radius: 10px !important; color:white; font-size: 0.9rem; text-align: left !important;">Идет сейчас</div>
                                  </template>
                                </v-progress-linear>
                                
                                    <v-chip
                                    style="justify-content: center;"
                                        color="grey"
                                        text-color="white"
                                        class="chip"
                                        v-else-if="subevent.isActive == 2"
                                        label
                                        >
                                        Окончено
                                    </v-chip>
                                    <v-chip
                                        style="justify-content: center;"
                                        color="blue"
                                        text-color="white"
                                        class="chip"
                                        v-else-if="subevent.isActive == 0"
                                        label
                                        >
                                        Ожидается
                                    </v-chip>
                                </v-list-item-content>
                            </v-list-item>
                    </v-expansion-panel-content>
                    </v-expansion-panel>
                </v-expansion-panels>
                </div>
            </v-expand-transition>
            <v-btn
            outlined
            rounded
            width="100%"
            style="margin-top: 10px;"
            @click="$router.push({ path: `/guests/${eventId}` , params: { title: mainEvent.mainTitle}})"
        >Список участников</v-btn>
        </v-card>
        </v-col>
    </v-row>
    </div>
    </v-container>

</template>

<script>
import axios from 'axios'
    export default {
        data() {
            return {
                mainEvent: {},
                date: [],
                dateFilt: [],
                show: true,
                eventId: '',
                isLoad: false,
                img: '',
            }
        },

        async mounted() {

            if (!this.$store.state.user || Object.keys(this.$store.state.user).length == 0) {
            this.$fire({
                title: "Уведомление",
                text: "Необходимо авторизоваться!",
                type: "error",
            })
            .then(this.$router.push('/howtouse'));
        }
            
            await axios.get(this.$store.state.getUrl + 'api/getSingleEvent/' + this.$route.params.id)
            .then(async res => {
                // console.log(res.data[0])
                this.mainEvent = res.data[0]
                this.$emit('update:title', await this.mainEvent.mainTitle)

                this.getBlue()
                this.eventId = this.mainEvent.id
            })
        },

        methods: {

            timeBar(start, stop) {
                var _start = new Date(start).getTime()
                var _stop = new Date(stop).getTime()
                var now = Date.now()
                console.log((((now - _start) / (_stop - _start)) * 100))
                return (((now - _start) / (_stop - _start)) * 100)
            },

            getBlue() {
                this.mainEvent.events.forEach(d => {

                    console.log(d)
                    if (new Date(d.timeStart) < Date.now() && new Date(d.timeStop) > Date.now()) {
                        d.isActive = 1
                        this.bar_date = d
                    }
                    else if (new Date(d.timeStop) < Date.now()) {
                        d.isActive = 2;
                    }
                    else if (new Date(d.timeStart) > Date.now()) {
                        d.isActive = 0;
                    }

                    d.subevents.forEach(d => {
                        console.log(d)
                        if (new Date(d.subTimeStart) < Date.now() && new Date(d.subTimeStop) > Date.now()) {
                            d.isActive = 1
                        }
                        else if (new Date(d.subTimeStop) < Date.now()) {
                            d.isActive = 2;
                        }
                        else if (new Date(d.subTimeStart) > Date.now()) {
                            d.isActive = 0;
                        }
                    })
                })
            },

            getWeekDay(date) {
                let days = ['ВС', 'ПН', 'ВТ', 'СР', 'ЧТ', 'ПТ', 'СБ'];
                return days[new Date(date).getDay() + 1];
            },
        }
    }
</script>

